import { Button, FormControl, FormLabel, Input, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, Radio, RadioGroup } from "@chakra-ui/react";
import { useContext } from "react";
import { GlobalContext } from "../../context";



export default function TransactionForm({onClose, isOpen}) {

    const {formData, setFormData, value, setValue, handleFormSubmit} = useContext(GlobalContext);

    function handleFormChange(event) {
        setFormData({...formData, [event.target.name] : event.target.value })
    }

    function handleSubmit(event) {
        event.preventDefault()
        handleFormSubmit(formData);
    }

    return <Modal isOpen={isOpen} onClose={onClose}>
        <form onSubmit={handleSubmit}>
            <ModalOverlay/>
            <ModalContent>
                <ModalHeader>Add New Transation</ModalHeader>
                <ModalCloseButton/>
                <ModalBody>
                    <FormControl>
                        <FormLabel> Enter Description</FormLabel>
                        <Input onChange={handleFormChange} placeholder="Enter Transaction description" name="description" type="text"/>
                    </FormControl>
                    <FormControl>
                        <FormLabel> Enter Amount</FormLabel>
                        <Input onChange={handleFormChange}  placeholder="Enter Transaction amount" name="amount" type="number"/>
                    </FormControl>
                    <RadioGroup mt={"5"} value={value} onChange={setValue}>
                        <Radio onChange={handleFormChange} checked={formData.type === "income"} value="income" colorScheme="blue" name="type">
                            Income
                        </Radio>
                        <Radio onChange={handleFormChange} checked={formData.type === "expense"}  value="expense" colorScheme="red" name="type">
                            Expense
                        </Radio>
                    </RadioGroup>
                </ModalBody>
                <ModalFooter>
                    <Button mr={"4"} onClick={onClose}>
                        Cancel
                    </Button>
                    <Button onClick={onClose} type="submit">
                        Add
                    </Button>
                </ModalFooter>
            </ModalContent>
        </form>
    </Modal>
}